<template>
  <div>
    <van-nav-bar title="首页"/>
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in banner" :key="item.id">
        <img :src="$preImg+item.img" alt="">
      </van-swipe-item>
    </van-swipe>
    <!-- 标签页 -->
    <van-tabs v-model="active">
      <van-tab title="热门推荐">
        <van-card
            :price="item.price"
            :title="item.goodsname"
            :thumb="$preImg+item.img"
            tag="hot"
            v-for="(item,index) in indexGoods[0].content"
            :key="index"
            >
          <!-- <template #tags>
            <van-tag plain type="danger">标签</van-tag>
            <van-tag plain type="danger">标签</van-tag>
          </template> -->
          <template #footer>
            <van-button size="small"  icon="cart-o" color="#07c160" @click="addGoods(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="发现新品">
         <van-card
            :price="item.price"
            :title="item.goodsname"
            :thumb="$preImg+item.img"
            tag="hot"
            v-for="(item,index) in indexGoods[1].content"
            :key="index"
            >
          <!-- <template #tags>
            <van-tag plain type="danger">标签</van-tag>
            <van-tag plain type="danger">标签</van-tag>
          </template> -->
          <template #footer>
            <van-button size="small"  icon="cart-o" color="#07c160" @click="addGoods(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="所有商品">
         <van-card
            :price="item.price"
            :title="item.goodsname"
            :thumb="$preImg+item.img"
            tag="hot"
            v-for="(item,index) in indexGoods[2].content"
            :key="index"
            >
          <!-- <template #tags>
            <van-tag plain type="danger">标签</van-tag>
            <van-tag plain type="danger">标签</van-tag>
          </template> -->
          <template #footer>
            <van-button size="small"  icon="cart-o" color="#07c160" @click="addGoods(item.id)"></van-button>
          </template>
        </van-card>
      </van-tab>
    </van-tabs>

  
  </div>
</template>

<script> 
import {bannerList,indexGoods,shoppAdd,listInfo} from "../util/request"
export default {
  data(){
    return {
      banner:[],
      active:0,
      indexGoods:[{content:''},{content:''},{content:''}],
      //存放添加到购物车的
    }
  },
  methods:{
    requestBanner(){
      bannerList().then(data=>{
        this.banner=data.list
      })
    },
    requestIndexgoods(){
      indexGoods().then(data=>{
        this.indexGoods=data.list
      })
    },
    addGoods(i){
      var user=JSON.parse(sessionStorage.getItem("user"))
      if(user){
        var a=user.uid
        var b=i
        listInfo({id:i}).then(res=>{

        })
        shoppAdd({uid:a,goodsid:b,num:1}).then(data=>{
          this.$dialog.alert({
            message: '添加成功！',
            }).then(() => {
            });
        })
      }else{
        this.$dialog.alert({
            message: '请先登录！',
            }).then(() => {
              this.$router.push('/login')
            });
      }
    }
  },
  mounted(){
    this.requestBanner()
    this.requestIndexgoods()
  }
}
</script>

<style scoped>
.my-swipe .van-swipe-item {
  height: 150px;
  }
  .my-swipe .van-swipe-item img{
    width: 100%;
  }
  .van-tab__pane{
    margin-bottom: 60px;
  }
</style>


